
import ScreenshotSlider from "./ScreenshotsSlider";
import StarRating from '@/app/components/StarRating'
type gameid = {
    params : {id: string}
}

export default async function gameid({params} : gameid) {
    const [gameRes , screenshotRes] = await Promise.all([
          fetch(`https://api.rawg.io/api/games/${params.id}?key=${process.env.RAWG_API_KEY}`),
          fetch(`https://api.rawg.io/api/games/${params.id}/screenshots?key=${process.env.RAWG_API_KEY}`),
    ])
    if(!gameRes.ok || !screenshotRes.ok) {
      throw new Error ('Failed to fetch the data ')
    }
    const game = await gameRes.json()
    const {results: screenshots} = await screenshotRes.json()

    return (
    <div className="container mx-auto px-4">
      <div className="flex flex-col lg:flex-row gap-8 py-20">
        {/* Left: Image with screenshots */}
        <div className="relative lg:w-1/2">
      <ScreenshotSlider screenshots={screenshots}/>
        </div>

        {/* Right: Game info */}
        <div className="lg:w-1/2 flex flex-col justify-start ml-10">
          <h1 className="text-4xl font-bold mb-6 text-center lg:text-left">{game.name}</h1>

          <div className="space-y-4">
            {game.released && (
              <p className="text-lg">
                <span className="font-semibold">Release Date:</span> {game.released}
              </p>
            )}
            {game.rating && (
            <StarRating rating={game.rating}/>
            )}
            {game.genres?.length > 0 && (
              <p className="text-lg">
                <span className="font-semibold">Genres:</span>{' '}
                {game.genres.map((genre: any) => genre.name).join(', ')}
              </p>
            )}
            {/* {game.description_raw && (
              <div className="mt-6">
                <h2 className="text-xl font-semibold mb-2">Description</h2>
                <p className="text-gray-700 leading-relaxed">{game.description_raw}</p>
              </div>
            )} */}
          </div>
        </div>
      </div>
    </div>
  );
}